import React from 'react';
import { Card, CardImg, CardText, CardBody, Breadcrumb, BreadcrumbItem,
    Button, Label, Modal, ModalHeader, ModalBody } from 'reactstrap';
import { Control, LocalForm, Errors } from 'react-redux-form'; 
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

// functions for form validation
const required = val => val && val.length; // checks for any data
const maxLength = len => val => !val || (val.length <= len); // has max length been exceeded
const minLength = len => val => val && (val.length >= len); // has min length been met
    

class CommentForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isModalOpen: false, // modal is closed at the start
            touched: {
                author: false
            } 
        };
        // bind correctly
        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

    } // end constructor

    // toggle modal window
    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    // handle modal form submit
    handleSubmit(values) {
        this.toggleModal();
        this.props.postComment(this.props.campsiteId, values.rating, values.author, values.text);
        //console.log('Current state is: ' + JSON.stringify(values));
        //alert('Current state is: ' + JSON.stringify(values));
    } // end submit

    render() {
      return (
        
        <React.Fragment>
            {/*button for modal window*/}
            <Button outline color="primary" onClick={this.toggleModal} className="mb-2">
                <i className="fa fa-pencil"/> Submit Comment
            </Button>

            {/*comment modal begins here*/}
            <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
            <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>

                <ModalBody> {/*pass form values to handleSubmit function*/}
                <LocalForm onSubmit={values => this.handleSubmit(values)}>

                        <div className="group">
                            <Label htmlFor="rating">Rating</Label>
                            <Control.select model=".rating" id="rating" name="rating"
                                className="form-control" defaultValue={'1'}>
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                                <option value="5">5</option>
                            </Control.select>
                        </div>
                        
                        <div className="group mt-2" >
                            <Label htmlFor="author">Your Name</Label>
                            <Control.text model=".author" id="author" name="author"
                                placeholder="Your Name"
                                className="form-control"
                                validators= {{ // validate name
                                    required,
                                    minLength: minLength(2),
                                    maxLength: maxLength(15)
                                }}
                            />
                            <Errors
                                className="text-danger"
                                model=".author"
                                show="touched"
                                component="div"
                                messages={{ // error messages for name
                                    required: 'Name Required',
                                    minLength: 'Must be at least 2 characters',
                                    maxLength: 'Must be 15 characters or less'
                                }}
                            />
                        </div>

                        <div className="group mt-2">
                            <Label htmlFor="text">Comment</Label>
                            <Control.textarea model=".text" id="text" name="text"
                                rows="6"
                                className="form-control">
                            </Control.textarea>
                        </div>
                        
                        <Button type="submit" value="submit" color="primary" className="mt-3">Submit</Button>
                    </LocalForm>
                </ModalBody>
            </Modal> {/*end modal*/}

        </React.Fragment>
      ); // end return
    } // end render
} // end class CommentForm

    // function for the clicked campsite image and description
   function RenderCampsite({campsite}) {
       
        return (
            <div className="col-md-5 m-1">
                <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                    <Card>
                        <CardImg top src={baseUrl + campsite.image} alt={campsite.name} />
                        <CardBody>
                            <CardText>{campsite.description}</CardText>
                        </CardBody>
                    </Card>
                </FadeTransform>
            </div>   
        ) // end return
    }  // end RenderCampsite function


    // function for the comments
    function RenderComments({comments, postComment, campsiteId}) {
        // if the comments exist then map out the 4 comments and format the date
        if(comments) {
            return (
                <div className="col-md-5 m-1">
                    <h4>Comments</h4>
                    <Stagger in delay={300}>
                        {comments.map(comment => {
                            return (
                                <Fade in key={comment.id}>
                                    <div>
                                        <p>{comment.text}<br />
                                            -- {comment.author}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}
                                        </p>
                                    </div>
                                </Fade>
                            );
                        })}
                    </Stagger>
                <CommentForm campsiteId={campsiteId} postComment={postComment} /> 
                </div> 
            ); // end return
        } // end if comments
        return <div /> // if not then return empty div
    } // end RenderComments function

    // display the clicked campsite image and description and comments
    function CampsiteInfo(props) {
        // if still loading then show the spinner
        if (props.isLoading) {
            return (
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        }
        // if an error then show the error message
        if (props.errMess) {
            return (
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <h4>{props.errMess}</h4>
                        </div>
                    </div>
                </div>
            );
        }
        // if the campsite exists then render the campsite and the comments
        if (props.campsite) {
            return(
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <Breadcrumb>
                                <BreadcrumbItem><Link to="/directory">Directory</Link></BreadcrumbItem>
                                <BreadcrumbItem active>{props.campsite.name}</BreadcrumbItem>
                            </Breadcrumb>
                            <h2>{props.campsite.name}</h2>
                            <hr />
                        </div>
                    </div>
                    <div className="row">
                        {/*get the clicked image and description*/}
                        <RenderCampsite campsite={props.campsite} />
                        {/*get the comments*/}
                        <RenderComments comments={props.comments} 
                                        postComment={props.postComment}
                                        campsiteId={props.campsite.id}/>
                    </div>
                </div> // end container
            );
        }
        return <div />; //if not then return empty div
    } // end CampsiteInfo function

export default CampsiteInfo;