import React from 'react';
import { Card, CardImg, CardImgOverlay, CardTitle, Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';


    // this return displays the 4 images
    // destructured functional component
    function RenderDirectoryItem({campsite}) {
        return (
            <Card>
                <Link to={`/directory/${campsite.id}`}>
                    <CardImg width="100%" src={baseUrl + campsite.image} alt={campsite.name} />
                    <CardImgOverlay>
                        <CardTitle>{campsite.name}</CardTitle>
                    </CardImgOverlay>
                </Link>
            </Card>
                
        ); // end return

    } // end function


    function Directory(props) {

        const directory = props.campsites.campsites.map(campsite => {
            // if still loading then show the spinner
            if (props.campsites.isLoading) {
                return (
                    <div className="container">
                        <div className="row">            
                            <Loading />
                        </div>
                    </div>
                );
            }
            // if an error then show the error message
            if (props.campsites.errMess) {
                return (
                    <div className="container">
                        <div className="row"> 
                            <div className="col">
                                <h4>{props.campsites.errMess}</h4>
                            </div>
                        </div>
                    </div>
                );
            }
            // display the campsite
            return (
                <div key={campsite.id} className="col-md-5 m-1">
                    <RenderDirectoryItem campsite={campsite} />
                </div>
            ); // end return
        }); // end const directory

        
        // this return displays the 4 campsite images
        return ( 
            
            <div className="container">
                <div className="row">
                    <div className="col">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                            <BreadcrumbItem active>Directory</BreadcrumbItem>
                        </Breadcrumb>
                        <h2>Directory</h2>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    {/*display the 4 campsite images*/}
                    {directory}
                </div>            
            </div>  
                      
        ); // end return
    } // end function Directory


export default Directory;